/* post related*/
.post-title { 
    margin: 0 auto;
    font-size: 32px;
}

.post-meta {
    margin-right: 5px;
    color: $grey-color;
    font-size: $small-font-size;
    a {
        color: inherit;
        &:hover {
            color: $hover-color;
        }
    }
}

.post-pagination {
    margin: 20px 0 0 0;
    display: flex;
    align-items: center; 
    justify-content: space-between;
    & > *{
        font-size: $small-font-size;
        color: $text-color;
    }
}

/* back-to-top button */
#back-top-button {
    display: none;
    color: #fff;
    padding: 8px 8px 0 8px;
    border-radius: 10px;
    background-color: lighten($grey-color, 25%);
    position: fixed;
    bottom: 100px;
    right: 250px;
    &:hover {
        background-color: darken($grey-color, 10%);
    }
}

/* table of contents related */
.toc {
    background-color: $code-backgroud-color;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 10px;
    //border: 1px solid $grey-color-light;
    font-size: $small-font-size;
}

.toc-meta {
    width: 150px;
    margin: 0;
    font-weight: 700;
    line-height: 1em;
    &:hover{
        cursor: pointer;
    }
}

.toc-meta::before {
    content: '\002B';
    color: $text-color;
    font-weight: bold;
    float: left;
    margin-left: 5px;
    margin-right: 10px;
}

.toc-active::before {
    content: '\2212';
}

.toc-content {  // only for display, no margin or padding.
    max-height: 0;
    width: auto;
    overflow: hidden;
    transition: max-height 0.2s ease;
}

.toc-list {
    margin-top: 10px;
    margin-bottom: 0;
    padding-left: 10px;
    li {
        margin: 0;
    }
}