// principle: do not set fixed padding or margin to the basic elements unless necessary. 
/**
    Reset some basic elements
**/
body, h1, h2, h3, h4,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}

/* common placeholders */
%clearfix {
    &::after {
        content: "";
        display: table;
        clear: both;
    }
}

%vertical-rhythm {
    margin-bottom: 0.75rem;
}

/**
    Basic styling
**/
body {
    font-family: $base-font-family;
    font-size: $base-font-size;
    line-height: 1.5;   // base-line-height unitless
    font-weight: $base-font-weight;
    color: $text-color;
    background-color: $background-color;
    -webkit-text-size-adjust: 100%;
}

/**
   vertical rhythm
   additional vertical rhythm for headings, figures, and tables
**/
h1, h2, h3, h4, h5, h6, p, ul, ol, dl, 
li, blockquote, pre, figure
{
    @extend %vertical-rhythm;
}

h1, h2, h3, h4, h5, h6, figure, table {
    margin-top: 1.5rem;
}


/**
    Headings
**/
h1 {font-size: $h1-font-size;}
h2 {font-size: $h2-font-size;}
h3 {font-size: $h3-font-size;}
h4, h5, h6 {font-size: $base-font-size;}
h1, h2, h3, h4, h5, h6 {
    font-weight: 700;   // same as bold
}


/**
    Links
**/
a {
    color: $brand-color;
    text-decoration: none;
    &:hover {
        color: $hover-color;
        text-decoration: underline;
    }
}


/**
    Images and Figures
**/
img {
    max-width: 100%;
    vertical-align: middle;
}
  
figure {
    //padding: 20px 0 20px 0;
    //margin-top: 0.75rem;
    & > img {
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    & > figcaption {
        font-size: $small-font-size;
        color: $grey-color;
        text-align: center;
        //padding: 2px;
    }
}
 



/**
    * Lists
**/
ul, ol {
    margin-left: 1rem;
}
  
li {
    > ul,
    > ol {
        margin-bottom: 0;
    }
}
  

/**
    Table, mod ???
**/
table { 
    padding: 0;
    margin-bottom: 5px;
    border-collapse: collapse; 
    font-size: $small-font-size;
}
table tr {
    border-top: 1px solid #cccccc;
    background-color: white;
    margin: 0;
    padding: 0;
}
table tr:nth-child(2n) { background-color: #f8f8f8; }
table tr th {
    font-weight: bold;
    border: 1px solid #cccccc;
    margin: 0;
    padding: 3px 6px;
}
table tr td {
    border: 1px solid #cccccc;
    margin: 0;
    padding: 3px 6px;
}
table tr th :first-child, table tr td :first-child { margin-top: 0; }
table tr th :last-child, table tr td :last-child { margin-bottom: 0; }


