@charset "utf-8";


// layout
$content-width:    800px;//960px;
$page-width:       960px;//1050px; no use???
$navbar-width:     1100px;

$on-palm:          600px;
$on-laptop:        800px;


// font
$base-font-family: 'Roboto', sans-serif;
$base-font-size:   16px;
$base-font-weight: 400; // or normal

$h1-font-size: calc($base-font-size * 1.75);        // 28px
$h2-font-size: calc($base-font-size * 1.5);         // 24px
$h3-font-size: calc($base-font-size * 1.25);        // 20px
$h4-font-size: $base-font-size;

$small-font-size:  calc($base-font-size * 0.875);   //14px
$footnote-font-size: calc($base-font-size * 0.75);  //12px
$tiny-font-size:   calc($base-font-size * 0.625);   //10px

$base-line-height: 1.5rem;  // no use ???
$line-height-mul: 1.5;      // multiplier no use ???


// color
$text-color:       #333333;
$background-color: #f5f5f5;
$brand-color:      #0e80c1;//#2a7ae2; rgb(39, 108, 218); #2583ad;
$hover-color:      #669900;//#4cc71e; #DD1144;
$code-backgroud-color: rgb(246,248,250); // #29281e;
$code-font-color: rgb(59,59,59);  // #eee; 

$grey-color:        #808080;
$nyu-indigo:        rgb(87,6,140);
$penn-carmine:      rgb(149,0,26);
$penn-blue:         rgb(1,40,113);
$iros-eggplant:     rgb(98,64,92);


// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "base",
        "layout_page",          // page layout, nav, footer
        "layout_pagination",    // pagination for plog
        "layout_post",          // post related
        "layout_about",         // aboutme
        "layout_category",
        "layout_archive",
        "layout_tag",
        "layout_pub"           // publication related
        //"layout_research"
        //"_syntax-highlighting-native" // "syntax-highlighting"
;

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap')