/* publications */
.pub-container {
    padding: 0;
    margin-bottom: 2rem;
    //background-color: #c4661f;
}

.pub-meta {
    line-height: 1em;
    margin: 0;  // cancel vertical ryhthm
    //color: $grey-color;
    //h4 {
        //font-weight: bold;
        //color: $text-color;
    //}
    b {
        color: $text-color;
        font-weight: normal;
    }
}

.pub-icon {
    padding: 5px 15px;
    margin: 10px;
    border-radius: 4px;
    border: 1px solid $text-color;
    font-size: $tiny-font-size;
    color: $text-color;
    &:hover {
        border: 1px solid $hover-color;
    }
}

.pub-content {    
    max-height: 0;
    margin: 0;
    overflow: hidden;
    max-width: 500px;
    //width: auto;
    transition: max-height 0.2s ease;
    p {
        font-size: $small-font-size;
        color: $text-color;
        line-height: 1.4em;
    }
}

