/* archives */
.archive-button {
	margin-left: 30px;
	width: 70px;
	&:hover{
        cursor: pointer;
    }
}

.archive-button::after {
    content: '\25BC';
    color: $text-color;
    font-weight: bold;
    float: right;
}

.archive-inactive::after {
    content: '\25BA';
}

.archive-content {
	margin-left: 30px;
	max-height: 0;
    width: auto;
    overflow: hidden;
    transition: max-height 0.2s ease;
	& > ul {
		list-style-type: none;
		& > li {
			margin: 0;
		}
	}
}
