/* pagination related */
.pagination {
    margin: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.pagination-pre-next {
    padding: 4px 8px;
    border-radius: 4px;
    background-color: #fff;
    color: $text-color;
    font-size: $small-font-size;
    /*&:hover {
        text-decoration: none;
        color: inherit;
    }*/
}

.pagination-icon {
    padding: 3px 10px;
    margin: 0 5px;
    color: $text-color;
    background-color: #fff;
    border-radius: 4px;
    &:hover {
        color: inherit;
        text-decoration: none;
    }
}

.no-hover-effect {
    &:hover {
        color: $text-color;
        text-decoration: none;
    }
}

.pagination-icon-selected {
    background-color: $brand-color;
    color: #fff;
}
