/* tags */
#tag-a {
    margin: 15px 10px;
    font-size: $small-font-size;
    &:hover {
        text-decoration: none;
    }
}

.tag-left {
    background-color: #5c5c5c;
    padding: 5px 10px;
    margin: 0;    
    border-radius: 4px 0 0 4px;
    color: #fff;
}

.tag-right {
    background-color: darken($background-color, 10%);
    padding: 5px 10px;
    margin-left: -5px;
    border-radius: 0 4px 4px 0;
    color: $text-color;
}
