/* main pages related*/
.card {
    display: block;
    min-height: 1px;
    margin: 30px auto;
    padding: 30px 40px;
    background-color: #ffffff;
    box-shadow: 0 4px 10px rgba(0,0,0,0.05), 0 0 1px rgba(0,0,0,0.1);
    border-radius: 4px;
}

.card-container{
    min-height: calc(100vh - 70px - 55px);  // minus navbar and footer
    max-width: $content-width;
    margin: 0 auto;
}


/* navigation bar related*/
.navbar {
    background-color: #ffffff;
    box-shadow: none;
    //box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
    border-bottom: 1px solid #e8e8e8;
}

.navbar-logo {
    height: 50px;
}

.navbar-container {
    max-width: $content-width;
    margin: 0 auto;
    padding: 10px 0;
    display: flex; 
    align-items: center; 
    justify-content: space-between;
}

.main-menu {
    list-style-type: none;
    margin: 0;
    padding: 0;
    & > li {
        display: inline-block;
        margin: 0 10px;
    }
    & > li > a {
        font-size: $h3-font-size;
        color: $text-color;
        &:hover {
            text-decoration: none;
        }
    }
    /*following is border bottom transition animation*/
    & > li::after {
        content: "";
        display: block;
	    margin: auto;
	    height: 2px;
	    width: 0px;
	    background: transparent;
	    transition: width .5s ease, background-color .5s ease;
    }
    & > li:hover::after {
        width: 100%;
        background: $grey-color;
    }
}


/*footer related*/
.footerbar {
    //min-height: 20px;
    display: flex;
    background-color: #ffffff;
}

.footerbar-container {
    max-width: $content-width;
    margin: 15px auto;
}

.footerbar-content {
    color: $grey-color;
    font-size: $footnote-font-size;
    & > * {
        color: inherit;
    }
}
