/**
    AboutMe (Home) page style
**/

.about-img {
    max-width: 60%;
    border: 1px solid #ddd; 
    border-radius: 20px; 
    padding: 5px;
    margin-top: 30px;
}

.about-name {
    margin-top: 20px;
    font-size: calc($base-font-size * 1.5);
    font-weight: 500;
}

.about-position {
    //padding: 10px 0;
    font-size: $small-font-size;
    line-height: 1.5;
}

.about-address {
    font-size: $small-font-size;
    //line-height: 1.5;
    //margin: 0 auto;
    display: block;
}

.about-icon a{
    display: inline-block;
    margin: 20px 8px;
    color: $nyu-indigo;
    font-size: calc($base-font-size*1.5);
    transition: 0.2s linear;
    &:hover {
        //font-size: calc($base-font-size*2);
        transform: scale(1.3);
        color: $nyu-indigo;
    }
}